import React, { Component } from "react";
import Link from "gatsby-link";

import db from '../api';

const byPropKey = (propertyName, value) => () => ({
  [propertyName]: value,
});

class SubmitFile extends Component {
  constructor(props){
    super(props)
    this.state = {
      name: '',
      email: '',
      message: '',
      file: ''
    }
  }
  
  onSubmit = (e) => {
    e.preventDefault;
    const {
      name,
      email,
      message
    } = this.state
    
    db.sendProposal(name, email, message, file);

  }
  render(){
    
    return ( 
    <form className="mx-auto md:w-1/2" onSubmit={this.onSubmit}>
      <p className="leading-loose mb-8">
        Here you may submit your csv/json file for us to prepare a chart for you.
      </p>
  
      <label
        className="block font-bold mb-2 text-xs uppercase"
        htmlFor="first-name"
      >
        Name
      </label>
      <input
        className="appearance-none block bg-grey-lighter mb-6 p-3 rounded-md text-grey-darker w-full"
        id="first-name"
        type="text"
        placeholder="Bill"
        onChange={event => this.setState(byPropKey('name', event.target.value))}
      />
  
      <label
        className="block font-bold mb-2 text-xs uppercase"
        htmlFor="last-name"
      >
        Email
      </label>
      <input
        className="appearance-none block bg-grey-lighter mb-6 p-3 rounded-md text-grey-darker w-full"
        id="last-name"
        type="text"
        placeholder="Murray"
        onChange={event => this.setState(byPropKey('email', event.target.value))}
      />
  
      <label className="block font-bold mb-2 text-xs uppercase" htmlFor="message">
        Describe what kind of chart you'd like
      </label>
      <textarea
        className="appearance-none bg-grey-lighter mb-6 p-3 rounded-md text-grey-darker w-full"
        placeholder="Say something..."
        rows="8"
      />
      <input type='file' />
      <button 
      className="border-b-4 border-grey-darker hover:border-grey-dark bg-grey-dark hover:bg-grey font-bold px-6 py-3 rounded text-sm text-white"
      type="submit">
        Submit
      </button>
    </form>
  );
  }
}

export default SubmitFile;



