import fetch from 'isomorphic-fetch';

export const getLAIcoData = () => {
  let cities = {};
  let graph = [];
  let data = [];
  let apiEndPoint =
    "https://data.lacity.org/api/views/5zqq-cys2/rows.json?accessType=DOWNLOAD";
  fetch(apiEndPoint)
    .then(function(response) {
      return response.json();
    })
    .then(function(myJson) {
      myJson.data.map(x => {
        if (cities.hasOwnProperty(x[12])) {
          cities[x[12]].size += 1;
        } else {
          cities[x[12]] = {
            label: x[12],
            size: 1
          };
        }
      });
      delete cities["LOS ANGELES"];
      Object.values(cities).map(x => {
        if (x.size == 1) {
          x.opacity = 0.1;
        } else if (x.size < 5) {
          x.opacity = 0.5;
        } else if (x.size < 10) {
          x.opacity = 0.7;
        } else {
          x.opacity = 0.99;
        }
        x.style = {
          border: "thin solid red"
        };
      });

      for (const prop in cities) {
        graph.push(cities[prop]);
      }
    });
  let result = {
    title: "LA pre ICO",
    color: 2,
    children: graph
  };
  return result;
};



